function DiluentInfo(props) {
    const classes = useStyles();
    /*
    const [diluentName, setDiluentName] = useState('');
    const handleChange_diluentName = (e) => {
        setDiluentName(e.target.value)
    }*/
    const handleChange_labware = (e) => {
        props.setDiluentLabware(e.target.value)

        const diluentInfo = {labware: e.target.value}

        APIService.updateDiluentInfo({diluent_info: diluentInfo, plate_id: props.activePlate, page_id: props.activePage, login_token: props.loginToken})
        .then(response => {
            console.log('DiluentInfo.js; updateDiluentInfo', response)
        })
    }

    return (
        <React.Fragment>
            <form className={classes.root} noValidate autoComplete="off">
                <Typography variant='h6'>Diluent Information</Typography>
                {/* 
                <TextField
                    id="outlined-text-DiluentName"
                    type="text"
                    label="Diluent Name"
                    value={diluentName}
                    onChange={handleChange_diluentName}
                    variant="outlined"
                />
                */}
                <TextField
                    id="outlined-select-source_labware"
                    select
                    label="Source Labware"
                    value={props.diluentLabware}
                    onChange={handleChange_labware}
                    variant="outlined"
                >
                    {source_labware.map((option) =>
                        <MenuItem key={option.value} value={option.value}>
                            {option.label}
                        </MenuItem>
                    )}
                </TextField>
                


            </form>
            <Divider/>
        </React.Fragment>
    )
}

export default DiluentInfo;