import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import {Switch, FormControlLabel} from '@material-ui/core';
import APIService from './utils/APIService';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
}));



function ButtonAppBar(props) {
    const classes = useStyles();
    
    const handleLogout = () => {
        APIService.logout()
            .then(response => {
                props.setCurrentUsername(response.username)
                props.setIsLoggedIn(response.logged_in)
                props.handleSetBlocks([''])
                props.setActivePlate('')
                props.setLoginToken(null)
            })
    }

    const handleSideBarOpen = () => {
        props.setSideBarOpen(!props.sideBarOpen)
    }

    return (
        <div className={classes.root}>
            <AppBar position="static">
                <Toolbar>
                    <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu" onClick={handleSideBarOpen}>
                        <MenuIcon />
                    </IconButton>
                   
                    {props.isLoggedIn ?
                        <Typography variant="h6" className={classes.title}>
                            Notes of {props.currentUsername}
                        </Typography>
                        :
                        <Typography variant="h6" className={classes.title}>
                            Log in to use the app
                        </Typography>
                    }
                    
                    {/*
                    <Button color="inherit">Save</Button>
                    
                    <FormControlLabel
                        control={
                            <Switch
                                checked={props.darkMode}
                                onChange={props.changeMode}
                                name="darkMode"
                                color="secondary"
                            />
                        }
                        label={props.darkMode ? "Dark Mode On" : "Dark Mode Off"}
                    
                    />*/}
                    {!props.isLoggedIn && (
                        <Button color="inherit" onClick={props.handleClickOpenSignUp}>Sign Up</Button>
                    )}
                    {!props.isLoggedIn && (
                        <Button color="inherit" onClick={props.handleClickOpen} variant="outlined">Login</Button>
                    )}
                    {props.isLoggedIn && (
                        <Button color="inherit" onClick={handleLogout} variant="outlined">Logout</Button>
                    )}

                </Toolbar>
            </AppBar>
        </div>
    );
}


export default ButtonAppBar;