class InducerSelector extends React.Component {
    constructor(props) {
        super(props);
        this.onChange_source_labware = this.onChange_source_labware.bind(this);
        this.onChange_stock_concentration = this.onChange_stock_concentration.bind(this);

        this.state = {
            activePlate: "",
            plateType: "",
            source_labware: null,
            stock_concentration: null,           
        }
    };

    componentDidMount() {
        this.setState({
            chemName: this.props.chemName,
            activePlate: this.props.activePlate,
        });
    }

    componentDidUpdate(prevProps, prevState) {
        const labwareChanged = prevState.source_labware !== this.state.source_labware;
        const stockConcChanged = prevState.stock_concentration !== this.state.stock_concentration;
        // const idChanged = prevState.id !== this.props.id;
        // const plateChanged = prevState.activePlate !== this.props.activePlate;
        
        const idChanged = this.state.id !== this.props.id;

        if (idChanged) {
            this.setState({
                activePlate: this.props.activePlate,

                id: this.props.id,
                name: this.props.name,
                low_conc: this.props.low_conc,
                high_conc: this.props.high_conc,
                n_conditions: this.props.n_conditions,
                exclude_neg_control: this.props.exclude_neg_control,
                log_scale: this.props.log_scale,

                // LABWARE
                source_labware: this.props.source_labware,
                stock_concentration: this.props.stock_concentration,
            })
        }
        
        if (labwareChanged || stockConcChanged) {
            this.props.updateChemical({
                id: this.props.id,
                name: this.props.name,
                low_conc: this.props.low_conc,
                high_conc: this.props.high_conc,
                n_conditions: this.props.n_conditions,
                exclude_neg_control: this.props.exclude_neg_control,
                log_scale: this.props.log_scale,

                // LABWARE
                source_labware: this.state.source_labware,
                stock_concentration: this.state.stock_concentration,
            })
        }
    }

    onChange_source_labware (e) {
        this.setState({source_labware: e.target.value})
    }
    
    onChange_stock_concentration (e) {
        this.setState({stock_concentration: e.target.value})
    }
    
    render() {
        const {classes} = this.props;
    return (
        <React.Fragment>
            <form className={classes.root} noValidate autoComplete="off">

            <Typography variant='h6'>Inducer Information: {this.props.name}</Typography>

            <TextField
                id="outlined-select-inducer_source_plate"
                select
                label="Source Labware"
                value={this.state.source_labware}
                // defaultValue={this.state.source_labware}
                onChange={this.onChange_source_labware}
                InputLabelProps={{ shrink: true }}
                variant="outlined"
            >
                {plateTypes['source_options'].map((option) =>
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                )}
            </TextField>

            <TextField
                id="outlined-number-inducer_stock_concentration"
                type="number"
                label="Stock Concentration (uM)"
                value={this.state.stock_concentration}
                // defaultValue={this.state.stock_concentration}
                onChange={this.onChange_stock_concentration}
                variant="outlined"
                InputLabelProps={{ shrink: true }}
            >
                {plateTypes['source_options'].map((option) =>
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                )}
            </TextField>

            </form>
            <Divider />
        </React.Fragment>
        
    );}
}

export default withStyles(useStyles)(InducerSelector);