import React, { useState, useEffect } from 'react';
import { Button, TextField, Dialog, DialogActions, DialogTitle, DialogContent, DialogContentText, Checkbox, FormControlLabel } from '@material-ui/core';
import APIService from './utils/APIService';

function LoginPage(props) {

    const [loginEmail, setLoginEmail] = useState('')
    const [loginPassword, setLoginPassword] = useState('')
    const [loginRemember, setLoginRemember] = useState(false)

    const onLoginRequest = () => {
        // console.log('LOGIN POST REQUEST HERE')
        APIService.PostLogin({ loginEmail, loginPassword, loginRemember })
            .then(response => {
                props.setCurrentUsername(response.username)
                props.setIsLoggedIn(response.logged_in)
                props.handleSetBlocks(response.blocks)
                
                console.log('===login_token===')
                console.log(response)
                console.log(response.login_token)
                props.setLoginToken(response.login_token)

                props.handleSetPages(response.pages)
                props.updateActivePage(response.page_id)
                console.log(response)
            })
            .catch(error => {
                console.log('loginError')
                console.log(error)})

        setLoginRemember(false)
        setLoginPassword('')
        setLoginEmail('')
        props.handleClose();
    };




    const handleChange_loginEmail = (e) => {
        setLoginEmail(e.target.value)
    }

    const handleChange_loginPassword = (e) => {
        setLoginPassword(e.target.value)
    }

    const handleChange_loginRemember = (e) => {
        setLoginRemember(e.target.checked)
    }

    const handleClose = () => {
        setLoginRemember(false)
        props.handleClose()
    }


    return (
        <React.Fragment>
            <Dialog open={props.open} onClose={handleClose} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Login</DialogTitle>
                <DialogContent>
                    {/*
                    <DialogContentText>
                        To subscribe to this website, please enter your email address here. We will send updates
                        occasionally.
                    </DialogContentText>
                    */}
                    <TextField
                        autoFocus
                        margin="dense"
                        id="name"
                        label="Email Address"
                        type="email"
                        fullWidth
                        onChange={handleChange_loginEmail}
                    />
                    <TextField
                        margin="dense"
                        id="password"
                        label="Password"
                        type="password"
                        fullWidth
                        onChange={handleChange_loginPassword}
                    />

                    <FormControlLabel
                        control={
                            <Checkbox
                                onChange={handleChange_loginRemember}
                                checked={loginRemember}
                            />
                        }
                        label="Remember Me"
                    />

                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color="primary">
                        Cancel
                    </Button>
                    <Button onClick={onLoginRequest} color="primary" variant="contained">
                        Login
                    </Button>
                </DialogActions>
            </Dialog>
        </React.Fragment>
    );
}

export default LoginPage;