const themeInstance = {
    background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
};

const API_URL_AWS = 'http://ec2-3-145-8-106.us-east-2.compute.amazonaws.com'
//const API_URL_AWS = ''

const useStyles = makeStyles((theme) => ({
    root: {
        background: '#eee',
        border: 0,
        fontSize: 16,
        borderRadius: 3,
        textAlign: 'center',
        // boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
        color: 'black',
        height: 64,
        padding: "20px"
        //marginTop: "20px"
        // padding: '20px',
    },
    fillAll: {
        height: "100%",
        width: "100%",
        //padding: "20px"
        //marginTop: "20px",
    },
    marginAll: {
        padding: "0px",
        '&:hover': {
            cursor: "pointer",
        }
    }
}));


function FileUpload(props) {
    const classes = useStyles();

    const [uploadInput, setUploadInput] = useState('');
    const [fileName, setFileName] = useState('');


    const onUpload = (files) => {
        const data = new FormData();
        data.append('file', files[0]);
        data.append('file_name', files[0].name)

        fetch(`${API_URL_AWS}/upload_data/${props.activePlate}`, {
            method: 'POST',
            credentials: 'include',
            body: data,
        }).then((response) => {
            console.log('reached first.then')
            props.setUploadToken(uid());
        })
    }

    return(
        
        <React.Fragment>
            <div className={classes.root}>
            <Dropzone onDrop={acceptedFiles => onUpload(acceptedFiles)}>
                {({ getRootProps, getInputProps }) => (
                        <section className={classes.fillAll}>
                        <div {...getRootProps()} className={classes.marginAll}>
                            <input {...getInputProps()}/>
                            <Typography variant="button">Drag and drop or click to upload your data in csv format</Typography>
                                <div><CloudUploadIcon fontSize="Large" /></div>
                        
                        </div>
                        
                    </section>
                )}
            </Dropzone>
            </div>       
        </React.Fragment>
    );
}

export default FileUpload;

