import React, { useState } from 'react';
import { Typography, Button, MenuItem, MenuList, ListItemText, ListItemIcon, Paper } from '@material-ui/core';

const MENU_HEIGHT = 150;
const allowedTags = [
    {
        id: "page-title",
        tag: "h1",
        label: "Page Title",
        isPlate: false
    },
    {
        id: "heading",
        tag: "h2",
        label: "Heading",
        isPlate: false
    },
    {
        id: "subheading",
        tag: "h3",
        label: "Subheading",
        isPlate: false
    },
    {
        id: "paragraph",
        tag: "p",
        label: "Paragraph",
        isPlate: false
    },
    {
        id: "plate-obj",
        tag: "",
        label: "Insert Plate",
        isPlate: true
    }
];

class plateOptions extends React.Component {
    constructor(props) {
        super(props);
        this.keyDownHandler = this.keyDownHandler.bind(this);
        this.state = {
            command: "",
            items: allowedTags,
            selectedItem: 0
        };
    }




    render() {
        // Define the absolute position before rendering
        const x = this.props.position.x;
        const y = this.props.position.y - MENU_HEIGHT;
        const positionAttributes = { top: y, left: x };

        return (
            <div className="SelectMenu" style={positionAttributes}>
                <div className="Items">
                    {this.state.items.map((item, key) => {
                        const selectedItem = this.state.selectedItem;
                        const isSelected = this.state.items.indexOf(item) === selectedItem;
                        return (
                            <div
                                className={isSelected ? "Selected" : null}
                                key={key}
                                role="button"
                                tabIndex="0"
                                onClick={() => this.props.onSelect(item.tag)}
                            >
                                {item.label}
                            </div>
                        );
                    })}
                </div>
            </div>
        );
    }
}

export default plateOptions;