import React, { useState } from 'react';
import { Button, TextField, Dialog, DialogActions, DialogTitle, DialogContent, DialogContentText, Typography } from '@material-ui/core';
import APIService from './utils/APIService';

function LoginPage(props) {
    const [emailError, setEmailError] = useState(false);
    const [nameError, setNameError] = useState(false);
    const [passwordError, setPasswordError] = useState(false);
    const [validate, setValidate] = useState(false);

    const [signupSuccessful, setSignupSuccessful] = useState(false);

    const [user_email, setUserEmail] = useState('');
    const [user_name, setUserName] = useState('');
    const [user_pass, setUserPass] = useState('');

    const onChange_user_email = (e) => {
        setUserEmail(e.target.value);
    }
    const onChange_user_name = (e) => {
        setUserName(e.target.value);
    }
    const onChange_user_pass = (e) => {
        setUserPass(e.target.value);
    }


    const onSignupRequest = () => {
        const signupRequest = {
            user_name: user_name,
            user_email: user_email,
            user_pass: user_pass,
        };
        APIService.sign_up(signupRequest)
            .then(response => {
                console.log(response)

                if (response.success) {
                    setUserEmail('')
                    setUserName('')
                    setUserPass('')

                    setValidate(false)

                    setSignupSuccessful(true)
                    //props.handleClose();
                }

                if (!response.success) {
                    setEmailError(response.user_email_error)
                    setPasswordError(response.user_pass_error)
                    setNameError(response.user_name_error)
                    setValidate(true)
                }

            });
    };

    const nameErrorMessage = {
        'length': 'Names need to be at least 5 characters long.',
        'exists': 'Username is already taken.'
    }

    const emailErrorMessage = {
        'format': 'Please enter a valid email address.',
        'exists': 'Email is already registered.'
    }

    const passwordErrorMessage = {
        'length': 'Passphrase must be at least 5 characters long.',
    }


    return (
        <React.Fragment>
            <Dialog open={props.open} onClose={props.handleClose} aria-labelledby="form-dialog-title">

                <DialogTitle id="form-dialog-title">Sign up</DialogTitle>


                <DialogContent>
                    {!signupSuccessful &&
                        <>
                            <DialogContentText>
                                Sign up for a new account to save your pages and plates for later.
                            </DialogContentText>

                            <TextField
                                required
                                error={validate && nameError}
                                onChange={onChange_user_name}
                                autoFocus
                                margin="dense"
                                id="username"
                                label="Username"
                                type="text"
                                fullWidth
                            />

                            {validate &&
                                <Typography>{nameErrorMessage[nameError]}</Typography>
                            }

                            <TextField
                                required
                                error={validate && emailError}
                                onChange={onChange_user_email}
                                margin="dense"
                                id="email"
                                label="Email Address"
                                type="email"
                                fullWidth
                            />

                            {validate &&
                                <Typography>{emailErrorMessage[emailError]}</Typography>
                            }



                            <TextField
                                required
                                error={validate && passwordError}
                                onChange={onChange_user_pass}
                                margin="dense"
                                id="password"
                                label="Password"
                                type="password"
                                fullWidth
                            />

                            {validate &&
                                <Typography>{passwordErrorMessage[passwordError]}</Typography>
                            }
                        </>}

                    {signupSuccessful &&
                        <>
                            <DialogContentText>
                                Sign up successful. Please log in to use all features.
                            </DialogContentText>
                        </>}
                </DialogContent>

                <DialogActions>
                    {!signupSuccessful &&
                        <>
                    <Button onClick={props.handleClose} color="primary">
                        Cancel
                    </Button>
                    <Button onClick={onSignupRequest} color="primary" variant="contained">
                        Sign Up
                    </Button>
                    </>}

                    {signupSuccessful &&
                        <>
                    <Button onClick={props.handleClose} color="primary" variant="contained">
                        Close
                    </Button>
                    </>}
                </DialogActions>
            </Dialog>
        </React.Fragment>
    );
}

export default LoginPage;