function ScriptSelector(props) {
    const classes = useStyles();

    
    const handleChange_destinationPlate = (e) => {
        props.setDestinationPlate(e.target.value)

        const newLoadingInfo = {
            destinationPlate: e.target.value,
            inducerMixVolume: props.inducerMixVolume,
            finalVolume: props.finalVolume,
            dilutionFactor: props.dilutionFactor,
        }
        APIService.updateLoadingInfo({ loadingInfo: newLoadingInfo, plate_id: props.activePlate, page_id: props.activePage, login_token: props.loginToken })
            .then(response => console.log('ScriptSelector.js; updateLoadingInfo: ', response))

    }
    
    const handleChange_inducerMixVolume = (e) => {
        props.setInducerMixVolume(e.target.value)
        const dilFactor = props.finalVolume / e.target.value;
        props.setDilutionFactor(dilFactor)

        const newLoadingInfo = {
            destinationPlate: props.destinationPlate,
            inducerMixVolume: e.target.value,
            finalVolume: props.finalVolume,
            dilutionFactor: dilFactor,
        }
        APIService.updateLoadingInfo({ loadingInfo: newLoadingInfo, plate_id: props.activePlate, page_id: props.activePage, login_token: props.loginToken })
            .then(response => console.log('ScriptSelector.js; updateLoadingInfo: ', response))

    }

    const handleChange_finalVolume = (e) => {
        props.setFinalVolume(e.target.value)
        const dilFactor = e.target.value / props.inducerMixVolume
        props.setDilutionFactor(dilFactor)

        const newLoadingInfo = {
            destinationPlate: props.destinationPlate,
            inducerMixVolume: props.inducerMixVolume,
            finalVolume: e.target.value,
            dilutionFactor: dilFactor,
        }
        APIService.updateLoadingInfo({ loadingInfo: newLoadingInfo, plate_id: props.activePlate, page_id: props.activePage, login_token: props.loginToken })
            .then(response => console.log('ScriptSelector.js; updateLoadingInfo: ', response))

    }

    const handleChange_dilutionFactor = (e) => {
        props.setDilutionFactor(e.target.value)
        const totalVol = props.inducerMixVolume * e.target.value;
        props.setFinalVolume(totalVol)

        const newLoadingInfo = {
            destinationPlate: props.destinationPlate,
            inducerMixVolume: props.inducerMixVolume,
            finalVolume: totalVol,
            dilutionFactor: e.target.value,
        }
        APIService.updateLoadingInfo({ loadingInfo: newLoadingInfo, plate_id: props.activePlate, page_id: props.activePage, login_token: props.loginToken })
            .then(response => console.log('ScriptSelector.js; updateLoadingInfo: ', response))
    }


    return (
        <React.Fragment>
            <form className={classes.root} noValidate autoComplete="off">
                <Typography variant='h6'>Loading Information</Typography>

                <TextField
                    id="outlined-select-destinationPlate"
                    select
                    label="Destination Plate"
                    value={props.destinationPlate}
                    onChange={handleChange_destinationPlate}
                    variant="outlined"
                >
                    {plateTypes[props.metadata.well_number].map((option) =>
                        <MenuItem key={option.value} value={option.value}>
                            {option.label}
                        </MenuItem>
                    )}
                </TextField>
                
                <TextField
                    id="outlined-number-destination_inducer_volume"
                    type="number"
                    label="Inducer Mix Volume"
                    value={props.inducerMixVolume}
                    onChange={handleChange_inducerMixVolume}
                    variant="outlined"
                />

                <TextField
                    id="outlined-number-destination_total_volume"
                    type="number"
                    label="Final Volume (post dilution)"
                    value={props.finalVolume}
                    onChange={handleChange_finalVolume}
                    variant="outlined"
                />

                <TextField
                    id="outlined-number-destination_concentration_factor"
                    type="number"
                    label="Dilution factor"
                    value={props.dilutionFactor}
                    onChange={handleChange_dilutionFactor}
                    variant="outlined"
                />


            </form>
            <Divider/>
        </React.Fragment>
    )
}

export default ScriptSelector;