import React from 'react';
import { TextField } from '@material-ui/core';

// import  { Typography } from '@material-ui/core';
import EditablePage from './NotionClone/EditablePage';
import APIService from './utils/APIService';

function Notes(props) {

    const handleChange_activePage_name = (e) => {
        props.setActivePage_name(e.target.value)
        APIService.updatePageName({page_id: props.activePage, page_name: e.target.value, login_token: props.loginToken})
        .then(response => {
            props.handleSetPages(response.pages)
        });
    }

    return (
        <React.Fragment>
            <div className="PageMenu">
                <TextField
                    id="page_name"
                    label="Page Name"
                    value={props.activePage_name}
                    onChange={handleChange_activePage_name}
                //value={props.destination_plate}
                //onChange={props.handleChange_destinationPlate}
                //variant="outlined"
                />

            </div>
            <div>
                {/*<Typography variant='h1'>This will be the notes app</Typography>*/}

                <EditablePage
                    activePlate={props.activePlate}
                    setActivePlate={props.setActivePlate}
                    updateActivePlate={props.updateActivePlate}
                    blocks={props.blocks}
                    handleSetBlocks={props.handleSetBlocks}
                    isLoggedIn={props.isLoggedIn}
                    handlePlateChange={props.handlePlateChange}

                    activePage={props.activePage}

                    loginToken={props.loginToken}

                />
            </div>
        </React.Fragment>
    );
}

export default Notes;