import React, {useState, useEffect} from 'react';
import { Typography, Button, Divider } from '@material-ui/core';
import FileUpload from './SetUpJS/FileUpload';
import SetUpMetadata from './SetUpJS/SetUpMetadata';
import SetUpChemdata from './SetUpJS/SetUpChemdata';
import APIService from './utils/APIService';

import uid from "./NotionClone/utils/uid";

const initialChemical = {
    name: "",
    low_conc: null,
    high_conc: null,
    n_conditions: null,
    exclude_neg_control: false,
    log_scale: true,

    // for labware setup:
    stock_concentration: null,
    source_labware: null,
}

function SetUp(props) {

    const addChemicalHandler = () => {
        const newChemical = {
            id: uid(),
            plate_id: props.currentPlate,
            name: "",
            low_conc: null,
            high_conc: null,
            conc_list: null,
            manualSetup: false,

            n_conditions: null,
            exclude_neg_control: false,
            log_scale: true,

            // for labware setup:
            stock_concentration: null,
            source_labware: null,
        }
        const updatedChemicals = [...props.chemicals, newChemical]
        props.setChemicals(updatedChemicals)
    }

    const deleteChemicalHandler = (currentChemical) => {
        const index = props.chemicals.map((chemical) => chemical.id).indexOf(currentChemical.id)
        const updatedChemicals = [...props.chemicals];
        updatedChemicals.splice(index, 1);
        props.setChemicals(updatedChemicals)
    }

    return (
        <React.Fragment>
            {/*<Typography variant='h2'>This will be the SetUp app</Typography>
            <Typography variant='h2'>Active Plate ID:</Typography>
            <Typography variant='h2'>{props.activePlate}</Typography>*/}
            {!props.activePlate &&
                <Typography>Select a plate to get started.</Typography>
            }
            
            {props.activePlate && 
            <SetUpMetadata
                loginToken={props.loginToken}
                
                activePlate={props.activePlate}
                activePage={props.activePage}

                metadata={props.metadata}
                setMetadata={props.setMetadata}
                
                well_number={props.well_number}
                setwell_number={props.setwell_number}
                replicate_number={props.replicate_number}
                setreplicate_number={props.setreplicate_number}
                blank_border={props.blank_border}
                setblank_border={props.setblank_border}
                />}
            {props.activePlate && props.chemicals.map((chemical, index) => {
                return(
                    <SetUpChemdata
                        id={chemical.id} 
                        name={chemical.name}
                        low_conc={chemical.low_conc}
                        high_conc={chemical.high_conc}

                        conc_list={chemical.conc_list}
                        manualSetup={chemical.manualSetup}

                        n_conditions={chemical.n_conditions}
                        exclude_neg_control={chemical.exclude_neg_control}
                        log_scale={chemical.log_scale}
                        index={index+1}
                        updateChemical={props.updateChemicalHandler}
                        // handlePlateChange={props.handlePlateChange}
                        deleteChemicalHandler={deleteChemicalHandler}
                        well_number={props.well_number}

                        activePlate={props.activePlate}

                        // for plate setup
                        stock_concentration={chemical.stock_concentration}
                        source_labware={chemical.source_labware}

                    />
                );
            }
            )}
            {props.activePlate &&

                <Divider />
            }
            {props.activePlate && 
            
            <Button onClick={addChemicalHandler} variant="outlined" color="primary">
                Add Chemical
            </Button>
            }
        </React.Fragment>
    );
}

export default SetUp;