import React, { useState, useEffect } from 'react';
import { Button, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import GetAppIcon from '@material-ui/icons/GetApp';

// import axios from 'axios';
import PipetteTypeSelector from './AutoLoadJS/PipetteTypeSelector'
import InducerSelector from './AutoLoadJS/InducerSelector'
import APIService from './utils/APIService';
import ScriptSelector from './AutoLoadJS/ScriptSelector';
import DiluentInfo from './AutoLoadJS/DiluentInfo';

const useStyles = makeStyles((theme) => ({
    root: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: '25ch',
        },
    },
}));

const API_URL_AWS = 'http://ec2-3-145-8-106.us-east-2.compute.amazonaws.com'
// const API_URL_AWS = ''


function AutoLoad(props) {
    const classes = useStyles();

    const [getMessage, setGetMessage] = useState({})



    const handleChange_pipetteLeft = (e) => {
        props.setPipetteLeft(e.target.value);
        props.setTipTypeLeft(e.target.tip_type);

        const pipetteSetup = {
            login_token: props.loginToken,
            plate_id: props.activePlate,
            pipette_left: e.target.value,
            tip_type_left: '',
            pipette_right: props.pipette_right,
            tip_type_right: props.tip_type_right,
            tips_left: props.tips_left,
            tips_right: props.tips_right,
        }

        APIService.update_pipette_setup(pipetteSetup)
        .then(response => console.log(response))
        console.log(e.target.tip_type)
        console.log(e.target.value)
    };

    const handleChange_pipetteRight = (e) => {

        const pipetteSetup = {
            plate_id: props.activePlate,
            pipette_left: props.pipette_left,
            tip_type_left: props.tip_type_left,
            pipette_right: e.target.value,
            tip_type_right: '',
            tips_left: props.tips_left,
            tips_right: props.tips_right,
        }

        props.setPipetteRight(e.target.value);
        props.setTipTypeRight(e.target.tip_type);
    };

    const handleChange_tipsLeft = (e) => {

        const pipetteSetup = {
            plate_id: props.activePlate,
            pipette_left: props.pipette_left,
            tip_type_left: props.tip_type_left,
            pipette_right: props.pipette_right,
            tip_type_right: props.tip_type_right,
            tips_left: e.target.value,
            tips_right: props.tips_right,
        }

        props.setTipsLeft(e.target.value)
    }
    const handleChange_tipsRight = (e) => {

        const pipetteSetup = {
            plate_id: props.activePlate,
            pipette_left: props.pipette_left,
            tip_type_left: props.tip_type_left,
            pipette_right: props.pipette_right,
            tip_type_right: props.tip_type_right,
            tips_left: props.tips_left,
            tips_right: e.target.value,
        }

        props.setTipsRight(e.target.value)
    }

    // INDUCER SETUP
    // const [destination_plate, setDestinationPlate] = useState('')


    const [source_labware, setSourceLabware] = useState('')
    const [water_source_labware, setWaterSourceLabware] = useState('')

    const handleChange_sourceLabware = (e) => {
        setSourceLabware(e.target.value)
    };

    const handleChange_waterSourceLabware = (e) => {
        setWaterSourceLabware(e.target.value)
    };


    // form validation
    /*

    const [formValidate, setFormValidate] = useState(false)
    const [submitReady, setSubmitReady] = useState(false);

    const onFormValidate = () => {
        setFormValidate(true)
    }

    // ready to download ot_script?

    useEffect(() => {
        if ((plateType && well_number > 0)) {
            setSubmitReady(true)
        } else { setSubmitReady(false) }
        // console.log('triggered useEffect: ')
        // console.log(submitReady)
    }, [props.metadata.plateType, well_number, submitReady])

    */

    // DOWNLOAD
    const onDownload = () => {
        const autoload_metadata = {
            pipette_left: props.pipette_left,
            pipette_right: props.pipette_right,
            tips_left: props.tips_left,
            tips_right: props.tips_right,
            dest_labware: props.destinationPlate,
            source_labware: source_labware,  // this one still needs amending / fixing. how to cope with more chemicals etc.
            diluent_labware: props.diluentLabware,
        }
        const plateInfo = {
            metadata: props.metadata,
            chemicals: props.chemicals,
            plate_id: props.activePlate,
            autoload_metadata: autoload_metadata,
            login_token: props.loginToken,
        };
        // setFormValidate(false)
        APIService.process_template(plateInfo)
            .then(response => {
                console.log(response)
                if (response.success) {
                    console.log('success scenario')
                    const link = document.createElement("a");
                    link.download = 'ot_script_reactname.py';  // this is not working with Flask backend atm...
                    link.href = `${API_URL_AWS}/download`;  // changeInDeployment
                    console.log(link.href)
                    link.click();
                }
                else {
                    console.log('failure scenario')
                }
            })
            .catch(error => console.log(error))

    };


    return (
        <React.Fragment>
            {!props.activePlate &&
            <Typography>Select a plate to get started.</Typography>
            }


            {props.activePlate &&
            <ScriptSelector
                // well_number={props.well_number}
                loginToken={props.loginToken}
                
                activePlate={props.activePlate}
                activePage={props.activePage}

                metadata={props.metadata}
                
                // autoload metadata
                destinationPlate={props.destinationPlate}
                inducerMixVolume={props.inducerMixVolume}
                finalVolume={props.finalVolume}
                dilutionFactor={props.dilutionFactor}
                
                setDestinationPlate={props.setDestinationPlate}
                setInducerMixVolume={props.setInducerMixVolume}
                setFinalVolume={props.setFinalVolume}
                setDilutionFactor={props.setDilutionFactor}

            />
            }

            {props.activePlate &&
            <PipetteTypeSelector
                activePlate={props.activePlate}
                chemicals={props.chemicals}
                metadata={props.metadata}
                pipette_left={props.pipette_left}
                pipette_right={props.pipette_right}
                tip_type_left={props.tip_type_left}
                tip_type_right={props.tip_type_right}
                tips_left={props.tips_left}
                tips_right={props.tips_right}
                handleChange_pipetteLeft={handleChange_pipetteLeft}
                handleChange_pipetteRight={handleChange_pipetteRight}
                handleChange_tipsLeft={handleChange_tipsLeft}
                handleChange_tipsRight={handleChange_tipsRight}
                well_number={props.well_number}
                setTipTypeLeft={props.setTipTypeLeft}
                setTipTypeRight={props.setTipTypeRight}
            />
            }

            {props.activePlate &&
                <DiluentInfo
                    loginToken={props.loginToken}

                    activePlate={props.activePlate}
                    activePage={props.activePage}

                    diluentLabware={props.diluentLabware}
                    setDiluentLabware={props.setDiluentLabware}
                />
            }

            {props.activePlate && props.chemicals.map((chemical, index) => {
                return(
                <InducerSelector
                    id={chemical.id}
                    index={index+1}
                    activePlate={props.activePlate}
                    metadata={props.metadata}
                    source_labware={chemical.source_labware}
                    stock_concentration={chemical.stock_concentration}
                    //water_source_labware={water_source_labware}
                    handleChange_sourceLabware={handleChange_sourceLabware}
                    //handleChange_waterSourceLabware={handleChange_waterSourceLabware}
                    well_number={props.well_number}

                    // chemical props
                    name={chemical.name}
                    updateChemical={props.updateChemicalHandler}
                    low_conc={chemical.low_conc}
                    high_conc={chemical.high_conc}
                    n_conditions={chemical.n_conditions}
                    exclude_neg_control={chemical.exclude_neg_control}
                    log_scale={chemical.log_scale}
                />
                );
            }
            )}
            {props.activePlate &&
            <Button
                onClick={onDownload}//{submitReady ? onDownload : onFormValidate}
                variant="contained"
                color="primary"
                size="medium"
                className={classes.button}
                startIcon={<GetAppIcon />}
                // sx={{m: 2}}
            >
                Download OT-2 Script
            </Button>
            }
        </React.Fragment>
    );
}

export default AutoLoad;
