import React, {useState} from 'react';
import { Typography } from '@material-ui/core';
import TestFlask from './TestFlask';
import FileUpload from './Analyse/FileUpload';
import GenerateHeatmap from './Analyse/GenerateHeatmap';

function Analyse(props) {

    const [uploadReady, setUploadReady] = useState(false);
    const [uploadToken, setUploadToken] = useState('');
    const [image_token, setImageToken] = useState('');


    return (
        <React.Fragment>
            {!props.activePlate &&
                <Typography>Select a plate to get started.</Typography>
            }
            <FileUpload
                uploadReady={uploadReady}
                setUploadReady={setUploadReady}
                activePlate={props.activePlate}

                uploadToken={uploadToken}
                setUploadToken={setUploadToken}
            />

            <GenerateHeatmap
                loginToken={props.loginToken}

                activePlate={props.activePlate}
                chemicals={props.chemicals}
                well_number={props.well_number}

                image_token={image_token}
                setImageToken={setImageToken}

                windowTabChange={props.windowTabChange}
                uploadToken={uploadToken}

            />
        </React.Fragment>
    );
}
// can use Flask API to push seaborn byte object exports / plots from python (check medium post)

export default Analyse;