import './App.css';
import React, {useEffect, useState} from 'react';
import SplitPane, {Pane} from 'react-split-pane';
// components
import Inspector from './components/Inspector';
import Notes from './components/Notes';
import ButtonAppBar from './components/ButtonAppBar';
import LoginPage from './components/LoginPage';
import SignupPage from './components/SignupPage';
import SideBar from './components/SideBar';

import uid from "./components/NotionClone/utils/uid";
import APIService from './components/utils/APIService';
import { Typography } from '@material-ui/core';


// import { makeStyles } from '@material-ui/core/styles';

// import NavBar from './components/NavBar';

const styleTextPanel = { background: '#eee', overflowY: 'scroll' };
const styleSidePanel = { overflowY: 'scroll' };

// const initialBlock = { id: uid(), html: "log in to retrieve your page", tag: "p", isPlate: false };


function App() {
  // user name and is logged in
  const [currentUsername, setCurrentUsername] = useState('Anon')
  const [isLoggedIn, setIsLoggedIn] = useState(false)
  const [loginToken, setLoginToken] = useState(null)

  const [sideBarOpen, setSideBarOpen] = useState(false)

  // blocks to pass to page
  const [blocks, setBlocks] = useState([''])
  const handleSetBlocks = (new_blocks) => {
    setBlocks(new_blocks)
    console.log('app.js; handleSetBlocks: ', blocks)
  }

  // pages update upon login (and for page adding?)
  const [pages, setPages] = useState([''])
  const handleSetPages = (new_pages) => {
    setPages(new_pages)
    console.log('app.js; handleSetPages: ', pages)
  }


  // dark mode toggle state
  const [darkMode, setDarkMode] = useState(false);
  const changeMode = () => {
    setDarkMode((prev) => !prev)
  }

  // active plate tracking
  const [activePlate, setActivePlate] = useState(null);
  const updateActivePlate = (id) => {
    setActivePlate(id)
    console.log('app.js; updateActivePlate: ', activePlate)
  }

  // active page tracking
  const [activePage_name, setActivePage_name] = useState(null);
  const [activePage, setActivePage] = useState(null);
  const updateActivePage = (id) => {
    setActivePage(id)
    console.log('app.js; updateActivePage: ', id)
    APIService.switchPage({ page_id: id, login_token: loginToken})
    .then(response => {
      handleSetBlocks(response.blocks)
    })
    updateActivePlate(null)
  }

  const [destinationPlate, setDestinationPlate] = useState('');
  const [inducerMixVolume, setInducerMixVolume] = useState('');
  const [finalVolume, setFinalVolume] = useState('');
  const [dilutionFactor, setDilutionFactor] = useState('');


  // tracking metadata and chemicals across plates
  const [chemicals, setChemicals] = useState([])
  const [metadata, setMetadata] = useState()

  const updateChemicalHandler = (currentChemical) => {
    // console.log('triggered updateChemicalHandler in SetUp.js')
    console.log('app.js; updateChemicalHandler: currentChemical: ', currentChemical)
    const index = chemicals.map((chemical) => chemical.id).indexOf(currentChemical.id)
    const updatedChemicals = [...chemicals];
    updatedChemicals[index] = {
      ...updatedChemicals[index],
      name: currentChemical.name,
      low_conc: currentChemical.low_conc,
      high_conc: currentChemical.high_conc,
      conc_list: currentChemical.conc_list,
      n_conditions: currentChemical.n_conditions,
      exclude_neg_control: currentChemical.exclude_neg_control,
      log_scale: currentChemical.log_scale,

      manualSetup: currentChemical.manualSetup,

      // for labware setup:
      stock_concentration: currentChemical.stock_concentration,
      source_labware: currentChemical.source_labware,

    };
    setChemicals(updatedChemicals);
    //setChemicals(updatedChemicals);

    console.log('app.js; updateChemicalHandler: updatedChemicals: ', updatedChemicals)


    //const oldMetadata = { blank_border: blank_border, well_number: well_number, replicate_number: replicate_number }
    // const oldChemicals = chemicals  // no idea why you need to do this const thing...
    // console.log('app.js; updateChemicalHandler: oldChemicals: ', oldChemicals)
    
    // setMetadata(metadata)
    console.log('app.js; updateChemicalHandler: metadata: ', metadata)
    if (activePlate) {
      console.log('triggered PostPlates')
      APIService.PostPlates({
        plate_id: activePlate,
        page_id: activePage,
        chemicals: updatedChemicals,
        login_token: loginToken
        //metadata: metadata
      }).then(response => console.log('app.js; updateChemicalHandler: ', response))
    };
  }
  /*
  useEffect(() => {
    // const oldMetadata = { blank_border: blank_border, well_number: well_number, replicate_number: replicate_number }
    const oldChemicals = chemicals  // no idea why you need to do this const thing...

    APIService.PostPlates({
      plate_id: activePlate,
      chemicals: oldChemicals,
      metadata: metadata,
    })

  }, [chemicals, activePlate, metadata])
  */
  const [well_number, setwell_number] = useState(96)
  const [blank_border, setblank_border] = useState(true)
  const [replicate_number, setreplicate_number] = useState(2)

  
  // FUNCTION STRUCTURE FOR PLATE UPDATE
  const handlePlateChange = (id) => {
    
    console.log('app.js; handlePlateChange: old plate: ', activePlate)
    console.log('app.js; handlePlateChange: new plate: ', id)
    /*
    const oldMetadata = {blank_border: blank_border, well_number: well_number, replicate_number: replicate_number}
    const oldChemicals = chemicals  // no idea why you need to do this const thing...
    //setMetadata(oldMetadata)
    console.log('old metadata to push:')
    console.log(oldMetadata)
    console.log('old chemicals to push:')
    console.log(oldChemicals)
    
    // API pushplate, if plate id exists.
    
    if (activePlate) {
      console.log('triggered PostPlates')
      APIService.PostPlates({
        plate_id: activePlate,
        chemicals: oldChemicals,
        metadata: oldMetadata
      })
    };*/
    // update plate_id
    setActivePlate(id)
    // API getplate
    if (id) {
      console.log('triggered app.js; GetPlate')
      APIService.GetPlate({plate_id: id, login_token: loginToken})
      // update State based on response
    .then(response => {
      //console.log('app.js; handlePlateChange: ', response)
      console.log('app.js; handlePlateChange, metadata: ', response.metadata)

      //console.log(chemicals)
      setChemicals(response.chemicals)
      //console.log('app.js; handlePlateChange, chemicals: ', response.chemicals)
      setMetadata(response.metadata)
      setwell_number(response.metadata['well_number'])
      setblank_border(response.metadata['blank_border'])
      setreplicate_number(response.metadata['replicate_number'])
      setDiluentLabware(response.diluent_info['diluent_labware'])

      setDestinationPlate(response.loading_info['destination_plate'])
      setInducerMixVolume(response.loading_info['inducer_mix_volume'])
      setFinalVolume(response.loading_info['final_volume'])
      setDilutionFactor(response.loading_info['dilution_factor'])


      // pipette setup if you want to save to a plate...
      /*
      setPipetteLeft(response.pipette_setup['pipette_left'])
      setPipetteRight(response.pipette_setup['pipette_right'])
      setTipTypeLeft(response.pipette_setup['tip_type_left'])
      setTipTypeRight(response.pipette_setup['tip_type_right'])
      setTipsLeft(response.pipette_setup['tips_left'])
      setTipsRight(response.pipette_setup['tips_right'])
      */
    }).catch(error => {
      console.log(error)
      //setwell_number(metadata['well_number'])
      //setblank_border(metadata['blank_border'])
      //setreplicate_number(metadata['replicate_number'])
    });
    // everything working, but state does not update fields in SetUp properly...
    // fix init by init by pushing to API at addBlock handler too.  
  };
  }




  const [open, setOpen] = useState(false);
  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const [openSignUp, setOpenSignUp] = useState(false);
  const handleClickOpenSignUp = () => {
    setOpenSignUp(true);
  };
  const handleCloseSignUp = () => {
    setOpenSignUp(false);
  };

  // ===================
  // AUTOLOAD CONSTANTS
  // ===================

  // PIPETTE SETUP
  const [pipette_left, setPipetteLeft] = useState('');
  const [pipette_right, setPipetteRight] = useState('');

  const [tip_type_left, setTipTypeLeft] = useState('');
  const [tip_type_right, setTipTypeRight] = useState('');

  const [tips_left, setTipsLeft] = useState('');
  const [tips_right, setTipsRight] = useState('');

  const [diluentLabware, setDiluentLabware] = useState('');


  
   //resizable split view
  return (
    
    <React.Fragment>
      
      <ButtonAppBar
        darkMode={darkMode}
        setDarkMode={setDarkMode}
        changeMode={changeMode}
        handleClickOpen={handleClickOpen}
        handleClickOpenSignUp={handleClickOpenSignUp}
        setCurrentUsername={setCurrentUsername}
        setIsLoggedIn={setIsLoggedIn}
        setActivePlate={setActivePlate}
        isLoggedIn={isLoggedIn}
        currentUsername={currentUsername}
        handleSetBlocks={handleSetBlocks}
        
        sideBarOpen={sideBarOpen}
        setSideBarOpen={setSideBarOpen}

        setLoginToken={setLoginToken}
        loginToken={loginToken}

      />

      <LoginPage
        open={open}
        handleClickOpen={handleClickOpen}
        handleClose={handleClose}
        
        handleSetBlocks={handleSetBlocks}
        setCurrentUsername={setCurrentUsername}
        setIsLoggedIn={setIsLoggedIn}

        setLoginToken={setLoginToken}
        loginToken={loginToken}

        updateActivePage={updateActivePage}
        handleSetPages={handleSetPages}
      />
      
      <SignupPage
        open={openSignUp} 
        handleClickOpen={handleClickOpenSignUp}
        handleClose={handleCloseSignUp}
        
        setCurrentUsername={setCurrentUsername}
        setIsLoggedIn={setIsLoggedIn}
        handleSetBlocks={handleSetBlocks}
      />
      { sideBarOpen &&
        <SideBar
          loginToken={loginToken}
        
          setSideBarOpen={setSideBarOpen}

          activePage={activePage}
          updateActivePage={updateActivePage}

          setActivePage_name={setActivePage_name}

          pages={pages}
          handleSetPages={handleSetPages}
        />
      }


      { isLoggedIn ?
      <div className="mainWindow">
      <SplitPane split="vertical" defaultSize = {'50%'} pane1Style = {styleTextPanel} pane2Style = {styleSidePanel}>
        <Pane>
          <Notes
            activePlate={activePlate}
            setActivePlate={setActivePlate}
            updateActivePlate={updateActivePlate}

            loginToken={loginToken}

            blocks={blocks}
            handleSetBlocks={handleSetBlocks}
            isLoggedIn={isLoggedIn}
            handlePlateChange={handlePlateChange}

            activePage={activePage}
            handleSetPages={handleSetPages}
            
            activePage_name={activePage_name}
            setActivePage_name={setActivePage_name}
          />
        </Pane>
        <Pane>
          <Inspector
            activePlate={activePlate}
            activePage={activePage}

            loginToken={loginToken}

            handlePlateChange={handlePlateChange}
            setChemicals={setChemicals}
            chemicals={chemicals}
            updateChemicalHandler={updateChemicalHandler}
            
            metadata={metadata}
            setMetadata={setMetadata}
            well_number={well_number}
            setwell_number={setwell_number}
            replicate_number={replicate_number}
            setreplicate_number={setreplicate_number}
            blank_border={blank_border}
            setblank_border={setblank_border}

            // autoload constants
            destinationPlate={destinationPlate}
            inducerMixVolume={inducerMixVolume}
            finalVolume={finalVolume}
            dilutionFactor={dilutionFactor}

            setInducerMixVolume={setInducerMixVolume}
            setDestinationPlate={setDestinationPlate}
            setFinalVolume={setFinalVolume}
            setDilutionFactor={setDilutionFactor}


            pipette_left={pipette_left}
            pipette_right={pipette_right}
            tip_type_left={tip_type_left}
            tip_type_right={tip_type_right}
            tips_left={tips_left}
            tips_right={tips_right}

            setPipetteLeft={setPipetteLeft}
            setPipetteRight={setPipetteRight}
            setTipTypeLeft={setTipTypeLeft}
            setTipTypeRight={setTipTypeRight}
            setTipsLeft={setTipsLeft}
            setTipsRight={setTipsRight}

            diluentLabware={diluentLabware}
            setDiluentLabware={setDiluentLabware}
          />
          </Pane>
      </SplitPane>
      </div>
      :
      <>
      <Typography variant='h2'>Please log in to use AutoLoad</Typography>
      </>
      }
    </React.Fragment>
  );
  
}

export default App;