import React, { useState } from 'react';
import { Typography, Button } from '@material-ui/core';
import plateOptions from './plateOptions'
import APIService from '../utils/APIService';


/* const [activePlate, setActivePlate] = useState('primary') */


class PlateObject extends React.Component {
    constructor(props) {
        super(props);
        /*
        this.openPlateMenuHandler = this.openPlateMenuHandler.bind(this);
        this.closePlateMenuHandler = this.closePlateMenuHandler.bind(this);
        */
        this.onSelectHandler = this.onSelectHandler.bind(this);
        // for menu handling
        this.closePlateOptions = this.closePlateOptions.bind(this);
        this.openPlateOptions = this.openPlateOptions.bind(this);
        this.onDeleteHandler = this.onDeleteHandler.bind(this);

        this.state = {
            isPlate: true,
            plateMenuIsOpen: false,
            // plateMenuPosition: null,
            isActivePlate: false,
            selectColor: "primary",
        };
    }

    openPlateOptions() {
        console.log(`open plate options for ${this.props.id} triggered in plateObject.js`)
        this.setState({plateMenuIsOpen: true});
        this.props.setActivePlate(false)
        document.addEventListener("click", this.closePlateOptions);
    }

    onDeleteHandler() {
        console.log(`plateObject.js; delete plate ${this.props.id}`)
        this.props.handlePlateChange(false)
        this.props.deletePlate(this.props.id)
        //APIService.deletePlate({plate_id: this.props.id})
    }

    closePlateOptions() {
        this.setState({plateMenuIsOpen: false});
        document.removeEventListener("click", this.closePlateOptions);
    }

    componentDidUpdate(prevProps, prevState) {
        const htmlChanged = prevState.html !== this.state.html;
        const tagChanged = prevState.tag !== this.state.tag;
        const isPlateChanged = prevState.isPlate !== this.state.isPlate;

        const activePlateChanged = prevProps.activePlate !== this.props.activePlate;

        // const plateMenuChanged = prevState.plateMenuIsOpen !== this.state.plateMenuIsOpen;

        if (htmlChanged || tagChanged || isPlateChanged || activePlateChanged) {
            this.props.updateBlock({
                id: this.props.id,
                html: this.state.html,
                tag: this.state.tag,
                isPlate: this.state.isPlate
                // plateMenuIsOpen: this.state.plateMenuIsOpen
            });
            this.props.activePlate === this.props.id ? this.setState({ selectColor: "secondary" }) : this.setState({ selectColor: "primary" }) 

            // console.log('triggered update function')
        }
    }


    onSelectHandler() {
        console.log('triggered onSelectHandler <plateObject/>')
        this.setState({ isActivePlate: true })
        this.props.handlePlateChange(this.props.id)
        //this.state.isActivePlate ? this.setState({ selectColor: "secondary" }) : this.setState({ selectColor: "primary" }) 
    }
    /*
    handleChange_activePlate() {
        if (activePlate === 'primary') {
            setActivePlate('secondary')
        } else {
            setActivePlate('primary')
        }
    }
    */
    render() {
        return (
            <React.Fragment>
                <Button
                    onClick={this.onSelectHandler}
                    variant='contained'
                    /*color={activePlate}*/
                    color={this.state.selectColor}
                    size='medium'
                >
                    Plate placeholder
                </Button>

 
                <Button
                    id='plateOption-button'
                    variant='contained'
                    color='transparent'
                    size='small'
                    aria-controls="plateOptions"
                    aria-haspopup="true"
                    aria-expanded={this.state.plateMenuIsOpen ? 'true' : undefined}
                    onClick={this.openPlateOptions}
                >
                    Plate options
            </Button>

                {this.state.selectColor === 'secondary' &&
                    <Button
                        onClick={this.onDeleteHandler}
                        //variant='contained'
                        /*color={activePlate}*/
                        color='secondary'
                        size='medium'
                    >
                        Delete Plate
                    </Button>
                }

            <plateOptions
                open={this.state.plateMenuIsOpen}
                onClose={this.closePlateOptions}
                anchorEl={this.state.plateMenuPosition}
                MenuListProps={{
                    'aria-labelledby': 'plateOption-button'
                }}
            />
            
            </React.Fragment>
        );
    }
}


