import React, {useState} from 'react';
import Drawer from '@mui/material/Drawer';
import Box from '@mui/material/Box';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';

import uid from './NotionClone/utils/uid';
import { Typography, Button } from '@mui/material';
import PageSelectObject from './PageSelectObject';
import APIService from './utils/APIService';

const SideBar = (props) => {
    const [openPosition, setOpenPosition] = useState(false);

    const handleSideBarClose = () => {
        props.setSideBarOpen(false)
    }

    const handlePageChange = (page_id) => {
        // console.log(page_id);
        props.updateActivePage(page_id);
        props.setSideBarOpen(false)

    }

    const addPageHandler = () => {
        const newPage = {id: uid(), login_token: props.loginToken}
        APIService.addNewPage(newPage)
        .then(response => {
            props.updateActivePage(response.activePage)
            props.handleSetPages(response.pages)
            props.setActivePage_name(response.name)
        })
    }

    // const [pageList, setPageList] = useState(['page1', 'page2', 'page3'])

    return (
        <Drawer
            open={'left'}
            anchor={'left'}
            onClose={handleSideBarClose}
        
        >
        <Box
            sx={{width: 250}}
        >
                <Typography variant='h6' sx={{ pt: 1, pl: 2 }} >Pages:</Typography>
                <Button onClick={addPageHandler}>
                    Add page
                </Button>
            <List>
            {props.pages.map((page, index) => (
                <PageSelectObject
                    handlePageChange={handlePageChange}
                    setActivePage_name={props.setActivePage_name}
                
                    name={page.name}
                    id={page.id}
                    
                />
                /*
                <ListItem button key={index} onClick={handlePageChange}>
                    <ListItemText primary={page.name} />
                </ListItem>
                */
            ))}
            </List>
        </Box>
        </Drawer>
    );
}

export default SideBar;