import React from 'react';
import PropTypes from 'prop-types'
import { Typography, Tab, Tabs, AppBar, Box } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
// import components
import SetUp from './SetUp';
import AutoLoad from './AutoLoad';
import Analyse from './Analyse';

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.paper,
    },
}));

function Inspector(props) {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        props.handlePlateChange(props.activePlate);
        setValue(newValue);
    };

    return (
        <div className={classes.root}>
            <AppBar position="static">
                <Tabs value={value} onChange={handleChange} centered>
                    <Tab label="Set Up" {...a11yProps(0)} />
                    <Tab label="AutoLoad" {...a11yProps(1)} />
                    <Tab label="Analyse" {...a11yProps(2)} />
                </Tabs>
            </AppBar>
            
            <TabPanel value={value} index={0}>
                <SetUp
                    loginToken={props.loginToken}

                    activePlate={props.activePlate}
                    activePage={props.activePage}
                    
                    setChemicals={props.setChemicals}
                    chemicals={props.chemicals}
                    updateChemicalHandler={props.updateChemicalHandler}
                    metadata={props.metadata}
                    setMetadata={props.setMetadata}
                    well_number={props.well_number}
                    setwell_number={props.setwell_number}
                    replicate_number={props.replicate_number}
                    setreplicate_number={props.setreplicate_number}
                    blank_border={props.blank_border}
                    setblank_border={props.setblank_border}

                    // handlePlateChange={props.handlePlateChange}

                />
            </TabPanel>

            <TabPanel value={value} index={1}>
                <AutoLoad
                    activePlate={props.activePlate}
                    activePage={props.activePage}

                    loginToken={props.loginToken}

                    chemicals={props.chemicals}
                    setChemicals={props.setChemicals}
                    updateChemicalHandler={props.updateChemicalHandler}
                    metadata={props.metadata}
                    well_number={props.well_number}

                    // ==================
                    // AUTOLOAD CONSTANTS
                    // ==================

                    // autoload metadata
                    destinationPlate={props.destinationPlate}
                    inducerMixVolume={props.inducerMixVolume}
                    finalVolume={props.finalVolume}
                    dilutionFactor={props.dilutionFactor}

                    setInducerMixVolume={props.setInducerMixVolume}
                    setDestinationPlate={props.setDestinationPlate}
                    setFinalVolume={props.setFinalVolume}
                    setDilutionFactor={props.setDilutionFactor}

                    // pipette setup
                    pipette_left={props.pipette_left}
                    pipette_right={props.pipette_right}
                    tip_type_left={props.tip_type_left}
                    tip_type_right={props.tip_type_right}
                    tips_left={props.tips_left}
                    tips_right={props.tips_right}
                    
                    setPipetteLeft={props.setPipetteLeft}
                    setPipetteRight={props.setPipetteRight}
                    setTipTypeLeft={props.setTipTypeLeft}
                    setTipTypeRight={props.setTipTypeRight}
                    setTipsLeft={props.setTipsLeft}
                    setTipsRight={props.setTipsRight}

                    // diluent metadata
                    diluentLabware={props.diluentLabware}
                    setDiluentLabware={props.setDiluentLabware}

                />
            </TabPanel>

            <TabPanel value={value} index={2}>
                {!props.activePlate &&
                    <Typography>Select a plate to get started.</Typography>
                }

                {props.activePlate && 
                <Analyse
                    loginToken={props.loginToken}
                
                    activePlate={props.activePlate}

                    chemicals={props.chemicals}
                    well_number={props.well_number}

                    windowTabChange={value}
                />
                }
            </TabPanel>
        </div>
    );
}

export default Inspector;