    static async sign_up(body) {
        try {
            const response = await fetch(`${API_URL_AWS}/sign_up`, {
                'method': 'POST',
                'credentials' : 'include',
                headers: {
                    'content-type': 'application/json'
                },
                body: JSON.stringify(body)
            })
            return await response.json()
        } catch (error) {
            return console.log(error)
        }
    }

