import React from "react";
import PropTypes from "prop-types";
import { Button, TextField, MenuItem, FormControlLabel, Checkbox, Divider, Typography } from '@material-ui/core';
import { Stack, Switch } from '@mui/material';

import { makeStyles, withStyles } from '@material-ui/core/styles';
import APIService from '../utils/APIService';

const wellNumbers = [
    {
        value: true,
        label: "log uniform",
    },
    {
        value: false,
        label: "uniform",
    }
]
const useStyles = (theme) => ({
    root: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: '25ch',
        },
    },
});


class SetUpChemdata extends React.Component {
    constructor(props) {
        super(props);
        this.onChange_log_scale = this.onChange_log_scale.bind(this);
        this.onChange_exclude_neg_control = this.onChange_exclude_neg_control.bind(this);
        this.onChange_low_conc = this.onChange_low_conc.bind(this);
        this.onChange_high_conc = this.onChange_high_conc.bind(this);
        this.onChange_n_conditions = this.onChange_n_conditions.bind(this);
        this.onChange_name = this.onChange_name.bind(this);
        this.onChange_manualSetup = this.onChange_manualSetup.bind(this);
        this.delete_chemical = this.delete_chemical.bind(this);
        this.onChange_conc_list = this.onChange_conc_list.bind(this);
        
        // this.activePlateMount = this.activePlateMount.bind(this);

        this.state = {
            id: null,
            name: "",
            low_conc: null,
            high_conc: null,
            conc_list: null,
            n_conditions: null,
            exclude_neg_control: false,
            log_scale: true,
            index: null,
            manualSetup: null,
        }
    };

    // load previously defined set up conditions and initial values
    componentDidMount() {
        console.log('SetUpChemdata.js; componentDidMount triggered.')
        console.log('COMPONENTDIDMOUNT LOGGED HERE')
        console.log('THIS is not triggering upon plate change, keeping state behind props.')
        this.setState({
            id: this.props.id,
            name: this.props.name,
            low_conc: this.props.low_conc,
            high_conc: this.props.high_conc,
            conc_list: this.props.conc_list,

            n_conditions: this.props.n_conditions,
            exclude_neg_control: this.props.exclude_neg_control,
            log_scale: this.props.log_scale,
            index: this.props.index,

            manualSetup: this.props.manualSetup,
            
            // for plate setup
            stock_concentration: this.props.stock_concentration,
            source_labware: this.props.source_labware,

        });
    }

    delete_chemical() {
        const pass_chem_info = { id: this.props.id }
        APIService.delete_chemical(pass_chem_info)
            .then(response => {
                console.log('SetUpChemicaldata.js; delete_chemical: ', response)
                this.props.deleteChemicalHandler({ id: this.props.id })
            })
    }

    componentDidUpdate(prevProps, prevState) {
        const nameChanged = prevState.name !== this.state.name;
        const low_concChanged = prevState.low_conc !== this.state.low_conc;
        const high_concChanged = prevState.high_conc !== this.state.high_conc;
        const n_conditionsChanged = prevState.n_conditions !== this.state.n_conditions;
        const exclude_neg_controlChanged = prevState.exclude_neg_control !== this.state.exclude_neg_control;
        const log_scaleChanged = prevState.log_scale !== this.state.log_scale;
        const indexChanged = prevState.index !== this.state.index;
        const manualSetup_changed = prevState.manualSetup !== this.state.manualSetup;
        const conc_listChanged = prevState.conc_list !== this.state.conc_list;

        const idChanged = this.state.id !== this.props.id;

        if (idChanged) {
            this.setState({
                id: this.props.id,
                name: this.props.name,
                low_conc: this.props.low_conc,
                high_conc: this.props.high_conc,
                conc_list: this.props.conc_list,

                n_conditions: this.props.n_conditions,
                exclude_neg_control: this.props.exclude_neg_control,
                log_scale: this.props.log_scale,
                index: this.props.index,

                manualSetup: this.props.manualSetup,
                
                // for plate setup
                stock_concentration: this.props.stock_concentration,
                source_labware: this.props.source_labware,
            });
        }

        if (nameChanged || low_concChanged || high_concChanged || n_conditionsChanged || exclude_neg_controlChanged || log_scaleChanged || manualSetup_changed || conc_listChanged) {
            // console.log('triggered ComponentDidUpdate in SetUpChemdata.js')

            const updatedChemical = {
                id: this.props.id,
                name: this.state.name,
                low_conc: this.state.low_conc,
                high_conc: this.state.high_conc,

                conc_list: this.state.conc_list,
                n_conditions: this.state.n_conditions,
                exclude_neg_control: this.state.exclude_neg_control,
                log_scale: this.state.log_scale,

                index: this.state.index,

                manualSetup: this.state.manualSetup,

                // for plate setup
                stock_concentration: this.props.stock_concentration,
                source_labware: this.props.source_labware,

            }

            console.log('SetUpChemdata.js; componentDidUpdate: updatedChemical: ', updatedChemical)

            this.props.updateChemical(updatedChemical);
        }

        // console.log('SetUpChemdata.js; componentDidUpdate: chemState: ', chemState)

    }


    onChange_exclude_neg_control(e) {
        this.setState({ exclude_neg_control: e.target.checked });
    }

    onChange_log_scale(e) {
        this.setState({ log_scale: e.target.value });
    }

    onChange_name(e) {
        this.setState({ name: e.target.value });
    }

    onChange_low_conc(e) {
        this.setState({ low_conc: e.target.value });
    }

    onChange_high_conc(e) {
        this.setState({ high_conc: e.target.value });
    }

    onChange_n_conditions(e) {
        this.setState({ n_conditions: e.target.value });
    }

    onChange_manualSetup(e) {
        this.setState({ manualSetup: !this.state.manualSetup });
    }

    onChange_conc_list(e) {
        this.setState({ conc_list: e.target.value });
    }

    render() {
        const { classes } = this.props;
        return (
            <React.Fragment>
                <Divider />
                <form className={classes.root} noValidate autoComplete="off">
                    <Typography variant='h6'>Chemical {this.state.index}: {this.state.name}</Typography>

                    <Stack direction="row" spacing={1} alignItems="center">
                        <Typography>Automatic Setup</Typography>
                        <Switch
                            checked={this.state.manualSetup}
                            onChange={this.onChange_manualSetup}
                        />
                        <Typography>Manual Setup</Typography>
                    </Stack>

                    {this.state.manualSetup ? <Typography>Manual</Typography> : <Typography>Automatic</Typography>}



                    {this.state.manualSetup ?
                        <>
                            <Typography>Manual Setup is active. This feature requires the user to input a list of comma separated values (e.g.: 0.1, 0.5, 0.8, 0.95). Replicates are still generated automatically from the plate metadata. Use the same units throughout (also for your stock solution for this chemical).</Typography>
                            <TextField
                                id="outlined-text-name"
                                label="Chemical"
                                variant="outlined"
                                required="true"
                                InputLabelProps={{ shrink: true }}
                                defaultValue={this.props.name}
                                value={this.props.name}
                                onChange={this.onChange_name}
                            />

                            <TextField
                                id="outlined-text-name"
                                label="Manual Concentration Input"
                                variant="outlined"
                                required="true"
                                InputLabelProps={{ shrink: true }}
                                defaultValue={this.state.conc_list}
                                value={this.state.conc_list}
                                onChange={this.onChange_conc_list}
                            />


                        </>
                        :
                        <>
                            <TextField
                                id="outlined-text-name"
                                label="Chemical"
                                variant="outlined"
                                required="true"
                                InputLabelProps={{ shrink: true }}
                                defaultValue={this.props.name}
                                value={this.props.name}
                                onChange={this.onChange_name}
                            />

                            <TextField
                                id="outlined-number-low"
                                label="Lowest Concentration (uM)"
                                type="number"
                                variant="outlined"
                                required="true"
                                InputLabelProps={{ shrink: true }}
                                defaultValue={this.props.low_conc}
                                value={this.props.low_conc}
                                onChange={this.onChange_low_conc}
                            />

                            <TextField
                                id="outlined-number-high"
                                label="Highest Concentration (uM)"
                                type="number"
                                variant="outlined"
                                required="true"
                                InputLabelProps={{ shrink: true }}
                                defaultValue={this.props.high_conc}
                                value={this.props.high_conc}
                                onChange={this.onChange_high_conc}
                            />

                            <TextField
                                id="outlined-number-high"
                                label="Number of Conditions"
                                type="number"
                                variant="outlined"
                                required="true"
                                InputLabelProps={{ shrink: true }}
                                defaultValue={this.props.n_conditions}
                                value={this.props.n_conditions}
                                onChange={this.onChange_n_conditions}
                            />

                            <TextField
                                id="outlined-select-log_scale"
                                select
                                label="log_scale"
                                value={this.props.log_scale}
                                onChange={this.onChange_log_scale}
                                // helperText="Well number"
                                variant="outlined"
                                required="true"
                            // error={!formValidate || wellNumber ? '' : 'true'}
                            >
                                {wellNumbers.map((option) => (
                                    <MenuItem key={option.value} value={option.value}>
                                        {option.label}
                                    </MenuItem>
                                ))}
                            </TextField>

                            <FormControlLabel
                                control={
                                    <Checkbox
                                        onChange={this.onChange_exclude_neg_control}
                                        checked={this.props.exclude_neg_control}
                                    />
                                }
                                label="Exclude Negative Control"
                                labelPlacement="bottom"
                            />

                        </>
                    }



                </form>

                <Button
                    variant="text"
                    color="secondary"
                    onClick={this.delete_chemical}
                >
                    Delete Chemical
                </Button>


            </React.Fragment>
        );
    }
}

/*
SetUpChemdata.propTypes = {
    classes: PropTypes.object.isRequired,
};
*/
export default withStyles(useStyles)(SetUpChemdata)
// export default SetUpChemdata