import React, { useState, useEffect } from 'react';
import { Button, deprecatedPropType } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

import APIService from '../utils/APIService';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.paper,
        MaxWidth: '100%',
        height: 'auto',
        maxHeight: '400px',
        justifyContent: 'center',
        justifyItems: 'center',
        display: 'block',
        margin: 'auto',
        
    },
    topPad: {
        marginTop: "12px",
        marginBottom: "12px"
    }
}));



function GenerateHeatmap(props) {
    const classes = useStyles();

    const onClick = () => {
        const data = {
            well_number: props.well_number,
            chemicals: props.chemicals,
            plate_id: props.activePlate,
            login_token: props.loginToken,
        }
        APIService.generate_heatmap(data)
        .then((response) => {
            props.setImageToken(response.token)
        })
        .catch(error => {
            console.log(error)
            console.log('probably no csv uploaded.')
        })
    }

    const onDownload = () => {
        var element = document.createElement("a");
        var file = new Blob(
            [
                `get_img/${props.activePlate}/${props.image_token}`
            ],
            { type: "image/*" }
        );
        element.href = URL.createObjectURL(file);
        element.download = "image.png";
        element.click();
    }

    useEffect(() => {
        props.setImageToken('')
        onClick();
    }, [props.activePlate, props.windowTabChange, props.uploadToken])


    return(
        <React.Fragment>
            <div className={classes.topPad}>
                <Button
                    onClick={onClick}
                >
                    Preview Heatmap
                </Button>
                <Button
                    onClick={onDownload}
                    variant="outlined"
                    color="primary"
                >
                    Download Heatmap
                </Button>
            </div>
            {props.image_token && 
                <div>
                    <img className={classes.root} src={`get_img/${props.activePlate}/${props.image_token}`} />

                </div>
            }

        </React.Fragment>
    )


}

export default GenerateHeatmap;